const Categories = ({categories,onSelect}) => {  
  return (
    <div id="categories">
      <h2 className="yeon-sung-regular">  <br /></h2>
      <ul>
        {
          categories.map((item,idx)=>{
            return (
              <li key={idx}
                  onClick={()=>{onSelect(item)}}
              ></li>
            )
          })
        }
      </ul>
    </div>
  )
}

export default Categories